import { BigNumber, FixedNumber } from 'ethers';
import { JSBI, Percent } from '@pancakeswap-libs/sdk';

// add 10%
export function calculateGasMargin(value: BigNumber): BigNumber {
  return value
    .mul(BigNumber.from(10000).add(BigNumber.from(1000)))
    .div(BigNumber.from(10000));
}

// converts a basis points value to a sdk percent
export function basisPointsToPercent(num: number): Percent {
  return new Percent(JSBI.BigInt(Math.floor(num)), JSBI.BigInt(10000));
}

export const isEmptyValue = (text: string) =>
  BigNumber.isBigNumber(text)
    ? BigNumber.from(text).isZero()
    : text === '' || text.replace(/0/g, '').replace(/\./, '') === '';

export const formatTimeKey2 = (value: number) => {
  const date = new Date(value * 1000);
  return date.toLocaleDateString('zh-cn');
};

export function getTimeKey(time, baseTime, stakePeriod) {
  baseTime = baseTime.toNumber();
  stakePeriod = stakePeriod.toNumber();
  if (time < baseTime) {
    throw new Error('time < farm started time');
  }
  let passed = Math.round(time - baseTime);
  let round = Math.round(passed / stakePeriod);
  let end = baseTime + round * stakePeriod;
  if (end < time) {
    return end + stakePeriod;
  }
  return end;
}

export const calculateDailyReward = (
  hashrate: FixedNumber,
  dailyBtcPerHash: FixedNumber,
  btcPrice: FixedNumber
) => {
  const dailyEstimated = hashrate.mulUnsafe(dailyBtcPerHash);
  const powerPrice = FixedNumber.from(5846)
    .divUnsafe(FixedNumber.from(1000 * 1000 * 100))
    .mulUnsafe(FixedNumber.from(103))
    .divUnsafe(FixedNumber.from(100));
  const powerPerHashUnitDay = FixedNumber.from(60).mulUnsafe(
    FixedNumber.from(24)
  );
  let dailyBTCNetreward = dailyEstimated.subUnsafe(
    powerPerHashUnitDay
      .mulUnsafe(powerPrice)
      .mulUnsafe(hashrate)
      .divUnsafe(btcPrice)
  );
  let dailyEstimatedUSD = dailyBTCNetreward.mulUnsafe(btcPrice).round(6);
  dailyBTCNetreward = dailyBTCNetreward.round(6);
  return { btc: dailyBTCNetreward, usd: dailyEstimatedUSD };
};
const WEEEK_REWARD_MAP =[
  82.823529216,
  41.411764608,
  20.705882304,
  10.352941152,
  5.176470576,
  2.588235288,
  1.294117644,
  0.647058822,0];
const START_TIME = new Date('2021-04-02 20:00 GMT+0800');

export function getDailyRewardsByBlock(pair: string) {
  console.log(`get ${pair} rewards`);
  let now = Date.now();
  if (now<START_TIME.getTime()){
    now = START_TIME.getTime()+1000;
  }
  let index = Math.floor((now-START_TIME.getTime())/(7*86400*1000));
  if (index>=8){
    index =8;
  }
  return FixedNumber.from(WEEEK_REWARD_MAP[index]+"");
}
