export async function getTimeWeightedPrice(contract: any) {
  if (contract) {
    const data = await contract.getSwapData();
    if (BigNumber.from(0).eq(data)) {
      //0 indicates that swap didn't inited, we make it default 1e18/1e18 means one
      return BigNumber.from(10 ** 18 + '');
    }
    return data;
  }
  return undefined;
}

