import React, { FC, useEffect, useMemo, useState } from 'react';
import {
  Typography,
  makeStyles,
  Grid,
  Box,
  TextField,
  CircularProgress,
  Button,
  Divider
} from '@material-ui/core';
import { useFormik } from 'formik';
import LoadingButton from 'src/components/LoadingButton';
import { BigNumber, FixedNumber } from '@ethersproject/bignumber';
import { utils } from 'ethers';
import { Theme } from 'src/theme';
import { formatBalance, parseBalance, formatApy } from 'src/utils/format';
import { isEmptyValue, calculateDailyReward } from 'src/utils';
import currency from 'currency.js';
import { useTranslation } from 'react-i18next';
import { BTCB } from 'src/constants/token';
import { getPriceBySymbol } from 'src/utils/price';
import { tokenPriceLoadingAtom } from 'src/atoms';
import { useRecoilValue } from 'recoil';

const DISTRIBUTE_LINE = '0.6';

interface StakeContentProps {
  state: any;
}

const useStyles = makeStyles(theme => ({
  root: {},
  inputArea: {
    marginBottom: '15px'
  }
}));

const percentMap = [
  {
    title: '25%',
    value: 0.25,
    cal: [1, 4]
  },
  {
    title: '50%',
    value: 0.5,
    cal: [1, 2]
  },
  {
    title: '75%',
    value: 0.75,
    cal: [3, 4]
  },
  {
    title: '100%',
    value: 1,
    cal: [1, 1]
  }
];

const StakeContent: FC<StakeContentProps> = ({ state }) => {
  const classes = useStyles();

  const { t } = useTranslation();
  const {
    stokenAllowed,
    asyncApproveClick,
    asyncStakeClick,
    sourceToken,
    targetToken,
    sourceBalance,
    yourStakeAmount,
    sourceStakeAmount,
    tokenPrice,
    apy,
    usdRewardPerStake,
    tokenRewardPerStake,
    poolLoading
  } = state;

  const tokenPriceLoading = useRecoilValue(tokenPriceLoadingAtom);

  const formik = useFormik({
    initialValues: {
      amount: '0'
    },
    onSubmit: () => {}
  });

  const {
    handleChange,
    handleSubmit,
    isSubmitting,
    touched,
    values,
    errors,
    setFieldValue
  } = formik;

  const [estRewardTokenAmount, setEstRewardTokenAmount] = useState<
    FixedNumber
  >();
  const [estRewardUsd, setEstRewardUsd] = useState<FixedNumber>();

  useEffect(() => {
    if (tokenRewardPerStake && usdRewardPerStake && values.amount) {
      const amount = FixedNumber.from(
        utils.formatUnits(
          parseBalance(values.amount + '', sourceToken.decimals),
          sourceToken.decimals
        )
      );

      const ru = usdRewardPerStake.mulUnsafe(amount);

      setEstRewardUsd(ru);

      if (targetToken && tokenPrice) {
        const price = getPriceBySymbol(tokenPrice, targetToken.symbol);
        // console.log("----tokenRewardPerStake,usdRewardPerStake",
        //   tokenRewardPerStake+"",usdRewardPerStake+"",amount+"",price);
        setEstRewardTokenAmount(
          ru.divUnsafe(FixedNumber.from(price + ''))
        );
      }
    }
  }, [tokenRewardPerStake, usdRewardPerStake, values.amount, targetToken, tokenPrice, sourceToken]);

  return sourceBalance.loading ? (
    <CircularProgress />
  ) : (
    <form className={classes.root} onSubmit={handleSubmit}>
      <Typography variant="h6">
        {t('mine.staking.readyToBeStaked')}:{' '}
        {sourceBalance.result &&
          formatBalance(sourceBalance.result, sourceToken.decimals)}
      </Typography>
      <Box mt={2} className={classes.inputArea}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              type="number"
              name="amount"
              label={t('mine.staking.stakedAmountInput')}
              error={Boolean(touched.amount && errors.amount)}
              helperText={touched.amount && errors.amount}
              fullWidth
              required
              value={values.amount}
              onChange={handleChange}
              variant="outlined"
            />
          </Grid>
          <Grid item container xs={12} sm={6}>
            {percentMap.map(item => (
              <Grid key={item.value} item xs={3}>
                <Button
                  variant="contained"
                  onClick={() => {
                    const m = item.cal[0];
                    const d = item.cal[1];
                    setFieldValue(
                      'amount',
                      utils.formatUnits(
                        sourceBalance.result
                          .mul(BigNumber.from(m))
                          .div(BigNumber.from(d)),
                        sourceToken.decimals
                      )
                    );
                  }}
                >
                  {item.title}
                </Button>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Box>
      <Typography variant="body2">
        {t('mine.staking.estyDailyReward')} ({targetToken.name}) :{' '}
        {estRewardTokenAmount && !estRewardTokenAmount.isNegative()
          ? formatBalance(estRewardTokenAmount as any, 19, 8)
          : ''}
      </Typography>
      <Typography variant="body2">
        {t('mine.staking.estyDailyReward')} (USD) : 
        {estRewardUsd && !estRewardUsd.isNegative()
          ? '$' + formatBalance(estRewardUsd as any, 19, 4)
          : ''}
      </Typography>
      <Typography variant="body2">
        {t('mine.staking.apy')} : {apy ? apy + '%' : '-'}
      </Typography>
      <Box mt={2}>
        {(sourceBalance.result && sourceBalance.result.isZero()) ||
        isEmptyValue(values.amount + '') ? (
          <Button
            fullWidth
            color="secondary"
            disabled={true}
            size="large"
            type="submit"
            variant="contained"
          >
            {t('mine.staking.stake')}
          </Button>
        ) : parseBalance(
            //FixedNumber.fromString(values.amount + '').round() + '',
            values.amount + '',
            sourceToken.decimals
          ).gt(sourceBalance.result) ? (
          <Button
            fullWidth
            color="secondary"
            disabled={true}
            size="large"
            type="submit"
            variant="contained"
          >
            {t('mine.staking.insufficientBalance')}
          </Button>
        ) : stokenAllowed ? (
          <LoadingButton
            fullWidth
            color="secondary"
            // disabled={isSubmitting}
            pending={asyncStakeClick.loading}
            size="large"
            type="submit"
            variant="contained"
            onClick={async () => {
              await asyncStakeClick.execute(
                parseBalance(values.amount+"", sourceToken.decimals)
              );
              setFieldValue('amount', '0');
            }}
          >
            {t('mine.staking.stake')}
          </LoadingButton>
        ) : (
          <LoadingButton
            fullWidth
            color="secondary"
            // disabled={isSubmitting}
            pending={asyncApproveClick.loading}
            size="large"
            type="submit"
            variant="contained"
            onClick={() => asyncApproveClick.execute()}
          >
            {t('mine.staking.approve')}
          </LoadingButton>
        )}
      </Box>
      <Box mt={2}>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body2">
              {t('mine.staking.youStaked')} :{' '}
              {yourStakeAmount.loading || tokenPriceLoading
                ? 'loading'
                : formatBalance(yourStakeAmount.result, sourceToken.decimals)}
            </Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant="body2">
              {t('mine.staking.totalStaked')}:{' '}
              {sourceStakeAmount
                ? formatBalance(sourceStakeAmount, sourceToken.decimals)
                : '-'}
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body2">
              {t('mine.staking.youStaked')} (USD):{' '}
              {yourStakeAmount.loading || tokenPriceLoading
                ? 'loading'
                : currency(
                    FixedNumber.from(
                      '' + getPriceBySymbol(tokenPrice, sourceToken.symbol)
                    )
                      .mulUnsafe(
                        FixedNumber.from(
                          utils.formatUnits(
                            yourStakeAmount.result,
                            sourceToken.decimals
                          )
                        )
                      )
                      .toString()
                  ).format()}
            </Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant="body2">
              {t('mine.staking.totalStaked')} (USD):{' '}
              {sourceStakeAmount && tokenPrice
                ? currency(
                    FixedNumber.from(
                      '' + getPriceBySymbol(tokenPrice, sourceToken.symbol)
                    )
                      .mulUnsafe(
                        FixedNumber.from(
                          utils.formatUnits(
                            sourceStakeAmount,
                            sourceToken.decimals
                          )
                        )
                      )
                      .toString()
                  ).format()
                : '-'}
            </Typography>
          </Grid>
        </Grid>
      </Box>
      {/* <Box mt={2}>
        <LoadingButton
          fullWidth
          color="secondary"
          // disabled={isSubmitting}
          size="large"
          type="submit"
          variant="contained"
          onClick={() => asyncApproveClick.execute(BigNumber.from(0))}
        >
          Rollback Approve
        </LoadingButton>
      </Box> */}
    </form>
  );
};

export default StakeContent;
