import React, { FC, useCallback, useState, useEffect } from 'react';
import {
  Container,
  makeStyles,
  Box,
  Divider,
  Card,
  CardContent,
  CardHeader,
  Avatar,
  Select,
  MenuItem,
  Grid,
  Breadcrumbs,
  Link
} from '@material-ui/core';

import Page from 'src/components/Page';
import Header from 'src/components/Header';
import { Theme } from 'src/theme';
import StakeContent from './StakeContent';
import UnstakeContent from './UnstakeContent';
import { useStakeState } from 'src/hooks/useStakeState';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%'
    // paddingTop: theme.spacing(3),
    // paddingBottom: theme.spacing(3)
  },

  icon: {
    marginRight: 5
  },

  text: {
    marginRight: 15
  }
}));

type StakeTye = 'stake' | 'unstake';

const StakeView: FC = () => {
  const classes = useStyles();
  const state = useStakeState();
  const { sourceToken, targetToken, account } = state;
  const { t } = useTranslation();

  const [type, setType] = useState<StakeTye>('stake');

  const handleTypeChange = useCallback(e => {
    setType(e.target.value);
  }, []);

  return (
    <Page className={classes.root} title="Stake">
      <Container maxWidth={false}>
        <Header title={t('mine.title.second')} />
        <Breadcrumbs aria-label="breadcrumb">
        <Link color="inherit" href="/#/mine" onClick={()=>{}}>
          {t('menu.mine')}
        </Link>
        <Link color="textPrimary"
          aria-current="page"
        >
          {t("mine.page."+type)}
        </Link>
      </Breadcrumbs>
        <Box mt={3}></Box>
        <Divider />
        <Card>
          <CardHeader
            title={
              <Box display="flex" alignItems="center">
                <Box flexGrow={1}>
                  <Box display="flex" alignItems="center">
                    <span className={classes.text}>
                      {t('mine.staking.stake')}:
                    </span>{' '}
                    <Avatar
                      className={classes.icon}
                      src={sourceToken.logoURI}
                    />{' '}
                    <span className={classes.text}>{sourceToken.name}</span>
                    <span className={classes.text}>
                      {t('mine.staking.mines')}:
                    </span>{' '}
                    <Avatar
                      className={classes.icon}
                      src={targetToken.logoURI}
                    />{' '}
                    <span className={classes.text}>{targetToken.name}</span>
                  </Box>
                </Box>
                <Box>
                  <Select value={type} onChange={handleTypeChange}>
                    <MenuItem key="stake" value="stake">
                      {t('mine.staking.stake')}
                    </MenuItem>
                    <MenuItem key="unstake" value="unstake">
                      {t('mine.staking.unstake')}
                    </MenuItem>
                  </Select>
                </Box>
              </Box>
            }
          ></CardHeader>
          <CardContent>
            {!account && t('global.loginFirst')}
            {account && type === 'stake' && <StakeContent state={state} />}
            {account && type === 'unstake' && <UnstakeContent state={state} />}
          </CardContent>
        </Card>
      </Container>
    </Page>
  );
};

export default StakeView;
