import { useEffect } from 'react';
import { useSetRecoilState } from 'recoil';
import { tokenPriceListAtom, tokenPriceLoadingAtom } from 'src/atoms';
import { getTokenPrice, TokenPriceList } from 'src/service';

export function useFetchTokenPrice() {
  const setTokenPriceList = useSetRecoilState(tokenPriceListAtom);
  const setTokenPriceLoading = useSetRecoilState(tokenPriceLoadingAtom);

  useEffect(() => {
    setTokenPriceLoading(true);
    getTokenPrice().then(list => {
      setTokenPriceList(list);
    }).finally(() => {
      setTokenPriceLoading(false);
    });
  }, []);
}
