import React, { useCallback, useRef } from 'react';
import type { FC } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Button,
  SvgIcon,
  Typography,
  makeStyles,
  Box,
  Toolbar,
  Hidden,
  IconButton,
  Container,
  ButtonGroup,
  Menu,
  MenuItem
} from '@material-ui/core';
import LanguageIcon from '@material-ui/icons/Language';
import { Menu as MenuIcon } from 'react-feather';
import { useWeb3React } from '@web3-react/core';
import { useSetRecoilState } from 'recoil';
import { mobileNavOpenAtom, walletModalStatusAtom, accountModalStatusAtom } from 'src/atoms';
import i18n from 'src/i18n';

interface FooterProps {
  className?: string;
}

const useStyles = makeStyles((theme) => ({
  footer: {
    position: 'fixed',
    bottom: 0,
    left: 0,
    right: 0,
    backgroundColor: theme.palette.background.paper,
    boxShadow: '0px 5px 10px 0px rgb(255 255 255 / 70%)',
    paddingTop: 16,
    paddingBottom: 16
  }
}));

const Footer: FC<FooterProps> = ({ className, ...rest }) => {
  const classes = useStyles();
  const { account } = useWeb3React();
  const actionRef = useRef<any>(null);
  const setWalletModalStatus = useSetRecoilState(walletModalStatusAtom);
  const setAccountModalStatus = useSetRecoilState(accountModalStatusAtom);

  const connectWallet = useCallback(() => {
    setWalletModalStatus(true);
  }, [setWalletModalStatus]);

  const openAccountModal = useCallback(() => {
    setAccountModalStatus(true);
  }, [setAccountModalStatus]);

  const [anchorEl, setAnchorEl] = React.useState(null);
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Hidden lgUp>
      <Container maxWidth={false}>
        <Box
          display="flex"
          alignItems="center"
          justifyContent="space-between"
          flexGrow={1}
          className={classes.footer}
        >
          <Box ml={2}>
            {
              account
                ? <Button variant="contained" onClick={openAccountModal}>{`${account.slice(0, 4)}...${account.slice(-4)}`}</Button>
                : <Button variant="contained" color="primary" ref={actionRef} onClick={connectWallet}>Connect Wallet</Button>
            }
          </Box>
          <Box p={0}>
            <Button onClick={(event) => setAnchorEl(event.currentTarget)}>
              <LanguageIcon />
            </Button>
            <Menu
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={() => setAnchorEl(null)}
            >
              <MenuItem
                onClick={() => {
                  i18n.changeLanguage('zh');
                  setAnchorEl(null);
                }}
                selected={i18n.language === 'zh'}
              >‰∏≠Êñá</MenuItem>
              <MenuItem
                onClick={() => {
                  i18n.changeLanguage('en');
                  setAnchorEl(null);
                }}
                selected={i18n.language === 'en'}
              >EN</MenuItem>
            </Menu>
          </Box>
        </Box>
      </Container>
    </Hidden>
  );
};

Footer.propTypes = {
  className: PropTypes.string
};

export default Footer;
