import { getAddress } from '@ethersproject/address';
import { AddressZero } from '@ethersproject/constants';
import { Web3Provider, JsonRpcSigner } from '@ethersproject/providers';
import { Contract } from '@ethersproject/contracts';

import IBEP20 from 'src/constants/abis/IBEP20.json';
import FARMBTCST from 'src/constants/abis/FarmBTCST.json';
import TBTCChef from 'src/constants/abis/tBTCChef.json';
import TBTCESTPolicy from 'src/constants/abis/tBTCESTPolicy.json';
import MasterCollector from 'src/constants/abis/MasterCollector.json';

export function getSigner(
  library: Web3Provider,
  account: string
): JsonRpcSigner {
  return library.getSigner(account).connectUnchecked();
}

export function getProviderOrSigner(
  library: Web3Provider,
  account?: string
): Web3Provider | JsonRpcSigner {
  return account ? getSigner(library, account) : library;
}

export function isAddress(value: any): string | false {
  try {
    return getAddress(value);
  } catch {
    return false;
  }
}

export function getContract(
  address: string,
  ABI: any,
  library: Web3Provider,
  account?: string
): Contract {
  if (!isAddress(address) || address === AddressZero) {
    throw Error(`Invalid 'address' parameter '${address}'.`);
  }

  return new Contract(address, ABI, getProviderOrSigner(library, account));
}

export function getERC20Contract(
  address: string,
  library: Web3Provider,
  account?: string
) {
  return getContract(address, IBEP20.abi, library, account);
}

export function getFarmBTCSTContract(
  address: string,
  library: Web3Provider,
  account?: string
) {
  return getContract(address, FARMBTCST.abi, library, account);
}

export function getTBTCChefContract(
  address: string,
  library: Web3Provider,
  account?: string
) {
  return getContract(address, TBTCChef.abi, library, account);
}

export function getTBTCESTPolicyContract(
  address: string,
  library: Web3Provider,
  account?: string
) {
  return getContract(address, TBTCESTPolicy.abi, library, account);
}

export function getMCContract(
  address: string,
  library: Web3Provider,
  account?: string
) {
  return getContract(address, MasterCollector.abi, library, account);
}

