import { BigNumber } from 'ethers';
import { useAsync, useAsyncCallback } from 'react-async-hook';
import { MaxUint256 } from '@ethersproject/constants';
import { toastConfig } from 'src/config';
import { useCallback, useEffect, useState, useMemo } from 'react';
import { useQuery } from 'src/hooks/useQuery';
import { TokenList, BTCB, BTCST, TBTC,FarmingInfoMap,FarmingPidMap } from 'src/constants/token';
import { Token,EnTokenMining } from 'src/types/token';
import { getERC20Contract, getFarmBTCSTContract,getTBTCChefContract } from 'src/utils/contract';
import { useActiveWeb3React } from './useActiveWeb3React';
import { toast } from 'react-toastify';
import { useDailyPool } from './useDailyPool';
import { useRecoilValue } from 'recoil';
import { tokenPriceListAtom } from 'src/atoms';
const EXPAND_BASE = BigNumber.from(1e12);
const minAllowance = BigNumber.from(2)
  .pow(96)
  .sub(1); 
export const useStakeState = () => {
  const { account, library } = useActiveWeb3React();
  const query = useQuery();
  const tokenPrice = useRecoilValue(tokenPriceListAtom);
  const [stokenAllowed, setStokenAllowed] = useState<boolean>(false);
  const [txHappend,setTxHappend] = useState<number>(0);
  const [sourceToken, targetToken] = useMemo(() => {
    const pair = query.get('pair');
    if (!pair) {
      return [null, null];
    }

    return pair.split('/').map(name => {
      if (!TokenList[name]) {
        console.error('Invalid pair');
        return null;
      }

      return TokenList[name] as Token;
    });
  }, [query.get]);

  const {
    loading,
    farmingInfo,
    // sourceSupplyAmount,
    sourceStakeAmount,
    usdRewardPerStake,
    tokenRewardPerStake,
    apy
  } = useDailyPool(sourceToken, targetToken, txHappend);

  const stakingPid = useMemo(()=>{
    return sourceToken && sourceToken.symbol ? FarmingPidMap[sourceToken.symbol]:0;
  },[sourceToken]);

  const sourceTokenERC20Contract = useMemo(() => {
    return sourceToken && sourceToken.address
      ? getERC20Contract(sourceToken.address, library, account)
      : null;
  }, [sourceToken, library, account]);
  
  const farmContract = useMemo(() => {
    return farmingInfo
      ? (farmingInfo.type== EnTokenMining.DAILY ? 
          getFarmBTCSTContract(
            farmingInfo.address,
            library,
            account
        ):getTBTCChefContract(
            farmingInfo.address,
            library,
            account))
      : null;
  }, [targetToken, library, account]);


  const sourceBalance = useAsync<BigNumber>(() => {
    return sourceTokenERC20Contract.balanceOf(account);
  }, [sourceTokenERC20Contract, account, txHappend]);

  // TODO: diff  block/daily 


  const allowance = useAsync<BigNumber>(() => {
    return sourceTokenERC20Contract.allowance(
      account,
      farmingInfo ? farmingInfo.address : null
    );
  }, [
    sourceTokenERC20Contract,
    account,
    farmingInfo,
    txHappend
  ]);

  const asyncApproveClick = useAsyncCallback(
    async (amount: BigNumber = MaxUint256) => {
      
      const gasLimit = await sourceTokenERC20Contract.estimateGas.approve(farmingInfo.address, amount);
      const tx = await sourceTokenERC20Contract.approve(farmingInfo.address, amount, {
        gasLimit
      });
      
      if (tx) {
        await tx.wait();
        setTxHappend(txHappend + 1);
      }

      return true;
    }
  );

  const asyncStakeClick = useAsyncCallback(async (amount: BigNumber) => {
    console.log("staking",amount+"");
    let tx;
    try {
      if (farmingInfo.type == EnTokenMining.DAILY){
        const gasLimit = await farmContract.estimateGas.apiDepositToMining(amount);
        tx = await farmContract.apiDepositToMining(amount, {
          gasLimit: gasLimit.mul(105).div(100)
        });
      }
      if (farmingInfo.type == EnTokenMining.BLOCK){
        const gasLimit = await farmContract.estimateGas.deposit(stakingPid,amount);
        tx = await farmContract.deposit(stakingPid,amount, {
          gasLimit: gasLimit.mul(105).div(100)
        });
      }
    } catch (e) {
      // toast.error(e?.data?.message || 'Network error', toastConfig);
    }
    if (tx){
      await tx.wait();
      setTxHappend(txHappend + 1);
    }
  });

  const asyncUnstakeClick = useAsyncCallback(async (amount: BigNumber) => {
    let tx;
    if (farmingInfo.type === EnTokenMining.DAILY){
      const gasLimit = await farmContract.estimateGas.apiWithdrawLatestSToken(
        amount
      );
      tx = await farmContract.apiWithdrawLatestSToken(amount, {
        gasLimit: gasLimit.mul(105).div(100)
      });
    }
    if (farmingInfo.type === EnTokenMining.BLOCK){
      console.log(farmContract.withdraw.estimateGas);
      const gasLimit = await farmContract.estimateGas.withdraw(stakingPid,amount);
      tx = await farmContract.withdraw(stakingPid,amount, {
        gasLimit: gasLimit.mul(105).div(100)
      });
    }
    if (tx){
      await tx.wait();
      setTxHappend(txHappend + 1);
    }
  })

  useEffect(() => {
    setStokenAllowed(allowance.result && allowance.result.gte(minAllowance));
  }, [allowance.result, asyncApproveClick.result]);

  const poolInfo = useAsync<any>(() => {
    if (targetToken.symbol === TBTC.symbol) {
      return farmContract.poolInfo(stakingPid);
    }
    return undefined;
  },[farmContract, account, stakingPid, txHappend]);


  // const farmUserInfo = useAsync<any>(viewUserInfo, [farmContract, account]);
  const farmUserInfo = useAsync<any>(() => {
    if (targetToken.symbol === TBTC.symbol) {
      return farmContract.stakingInfo(stakingPid, account);
    } else {
      return farmContract.viewUserInfo(account);
    }
  }, [farmContract, account, stakingPid, txHappend]);


  const yourStakeAmount = useMemo(() => {
    const info = farmUserInfo.result;
    let result = info
      ? targetToken.symbol === TBTC.symbol
        ? info.amount
        : info.amount.add(info.lockedAmount)
      : BigNumber.from(0);
    if (poolInfo && info && targetToken.symbol === TBTC.symbol){
      if (poolInfo.result && poolInfo.result.stakedTokenShrinkFactor.gt(0)){
        result = result.mul(
          poolInfo.result.stakedTokenShrinkFactor).div(EXPAND_BASE);
      }
    }
    return { 
      loading: farmUserInfo.loading,
      error: farmUserInfo.error,
      result
    }
  }, [poolInfo,targetToken, farmUserInfo.result, farmUserInfo.loading, farmUserInfo.error]);
  
  return {
    stokenAllowed,
    asyncApproveClick,
    asyncStakeClick,
    asyncUnstakeClick,
    sourceToken,
    targetToken,
    account,
    sourceBalance,
    yourStakeAmount,
    sourceStakeAmount,
    // sourceSupplyAmount,
    tokenPrice,
    usdRewardPerStake,
    tokenRewardPerStake,
    apy,
    poolLoading: loading
  };
};
