import React, {
  useCallback,
  useRef,
} from 'react';
import type { FC } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Button,
  SvgIcon,
  Typography,
  makeStyles,
  Box,
  Toolbar,
  Hidden,
  IconButton
} from '@material-ui/core';
// import { Send as SendIcon } from 'react-feather';
import { Menu as MenuIcon } from 'react-feather';
import { useWeb3React } from '@web3-react/core';
import { useSetRecoilState } from 'recoil';
import { mobileNavOpenAtom, walletModalStatusAtom, accountModalStatusAtom } from 'src/atoms';
// import { useActiveWeb3React} from 'src/hooks/useActiveWeb3React';
import Logo from 'src/components/Logo';

interface HeaderProps {
  className?: string;
  title: string;
}

const useStyles = makeStyles((theme) => ({
  root: {
    // zIndex: theme.zIndex.drawer + 100,
    // backgroundColor: theme.palette.background.default
    padding: 0
  },
  account: {
    width: 80,
  },
  toolbar: {
    minHeight: 64
  }
}));

const Header: FC<HeaderProps> = ({ className, title, ...rest }) => {
  const classes = useStyles();
  const { account } = useWeb3React();
  const actionRef = useRef<any>(null);
  const setWalletModalStatus = useSetRecoilState(walletModalStatusAtom);
  const setAccountModalStatus = useSetRecoilState(accountModalStatusAtom);
  const setMobileNavOpen = useSetRecoilState(mobileNavOpenAtom);

  const showNav = useCallback(() => {
    setMobileNavOpen(true);
  }, [setMobileNavOpen])

  const connectWallet = useCallback(() => {
    setWalletModalStatus(true);
  }, [setWalletModalStatus]);

  const openAccountModal = useCallback(() => {
    setAccountModalStatus(true);
  }, [setAccountModalStatus]);

  return (
    <Toolbar className={clsx(classes.root, className)} {...rest}>
      <Box
        display="flex"
        alignItems="center"
        justifyContent="space-between"
        flexGrow={1}
        pt={3}
        pb={3}
      >
        <Hidden lgUp>
          <Logo />
          <IconButton
            color="inherit"
            onClick={showNav}
          >
            <SvgIcon fontSize="small" color="primary">
              <MenuIcon />
            </SvgIcon>
          </IconButton>
        </Hidden>
        <Hidden mdDown>
          <Box flexGrow={1}>
            <Typography variant="h3" color="textPrimary">
              {
                /* <SvgIcon fontSize="small">
                  <SendIcon />
                </SvgIcon> */
              }
              {title}
            </Typography>
          </Box>
          {/* <Box ml={2} flexGrow={1} /> */}
          <Box>
            {
              account
                ? <Button variant="contained" onClick={openAccountModal}>{`${account.slice(0, 4)}...${account.slice(-4)}`}</Button> 
                : <Button
                    variant="contained"
                    color="primary"
                    ref={actionRef}
                    onClick={connectWallet}
                  >
                    Connect Wallet
                  </Button>
            }
          </Box>
        </Hidden>
      </Box>
    </Toolbar >
  );
};

Header.propTypes = {
  className: PropTypes.string
};

export default Header;
