import { utils, BigNumber, FixedNumber } from 'ethers';
import { Token } from 'src/types/token';
import { useAsync } from 'react-async-hook';
import useAsyncEffect from 'use-async-effect';
import { getTokenPrice, getMineStatus, getPoolInfo } from 'src/service';
import { useActiveWeb3React } from './useActiveWeb3React';
import { getERC20Contract, getTBTCChefContract } from 'src/utils/contract';
import { useEffect, useMemo, useState } from 'react';
import { FarmingInfoMap, BTCB, TBTC, FarmingPidMap } from 'src/constants/token';
import { calculateDailyReward, getDailyRewardsByBlock } from 'src/utils';
import { formatApy } from 'src/utils/format';
import { getPriceBySymbol } from 'src/utils/price';
import { useRecoilValue } from 'recoil';
import { tokenPriceListAtom, tokenPriceLoadingAtom } from 'src/atoms';
import isEmpty from 'lodash/isEmpty';
import { contractConfig } from 'src/config';

const DISTRIBUTE_LINE = '0.6';

export function useDailyPool(
  sourceToken: Token,
  targetToken: Token,
  txHappend: number = 0
) {
  const { account, library } = useActiveWeb3React();
  const tokenPrice = useRecoilValue(tokenPriceListAtom);
  const tokenPriceLoading = useRecoilValue(tokenPriceLoadingAtom);
  const [loading, setLoading] = useState(true);
  const [sourceStakeAmount, setSourceStakeAmount] = useState<BigNumber>();
  const [tokenRewardPerStake, setTokenRewardPerStake] = useState<FixedNumber>();
  const [usdRewardPerStake, setUsdTokenRewardPerStake] = useState<
    FixedNumber
  >();

  const farmingInfo = useMemo(() => {
    return FarmingInfoMap[targetToken.symbol];
  }, [targetToken.symbol]);

  const sourceTokenERC20Contract = useMemo(() => {
    return sourceToken && sourceToken.address
      ? getERC20Contract(sourceToken.address, library, account)
      : null;
  }, [sourceToken, library, account]);

  useAsyncEffect(async () => {
    setLoading(true);
    if (tokenPriceLoading) {
      return;
    }

    try {
      if (targetToken.symbol === TBTC.symbol) {
        const contract = getTBTCChefContract(
          contractConfig.TBTC_CHEF,
          library,
          account
        );
        const poolInfo = await getPoolInfo(
          contract,
          FarmingPidMap[sourceToken.symbol]
        );
        let sourceStakeAmount = poolInfo.totalDepositAmount;
        if (sourceStakeAmount.isZero()) {
          return;
        }
        const stakedTokenShrinkFactor = poolInfo.stakedTokenShrinkFactor;
        if (stakedTokenShrinkFactor > 0) {
          sourceStakeAmount = sourceStakeAmount
            .mul(stakedTokenShrinkFactor)
            .div(BigNumber.from(1e12 + ''));
        }

        setSourceStakeAmount(sourceStakeAmount);

        const hashrate = FixedNumber.from(
          utils.formatUnits(sourceStakeAmount, sourceToken.decimals) + ''
        );
        const tokenReward = getDailyRewardsByBlock(
          `${sourceToken.symbol}-${targetToken.symbol}`
        );

        const price = FixedNumber.from(
          '' + getPriceBySymbol(tokenPrice, targetToken.symbol)
        );

        const tps = tokenReward.divUnsafe(hashrate);
        const ups = tps.mulUnsafe(price);

        setTokenRewardPerStake(tps);
        setUsdTokenRewardPerStake(ups);
      } else {
        const BTCBStakeAmount: BigNumber = await sourceTokenERC20Contract.balanceOf(
          contractConfig.FARM
        );
        const BTCSTStakeAmount: BigNumber = await sourceTokenERC20Contract.balanceOf(
          contractConfig.FARM_BTCST
        );

        setSourceStakeAmount(
          targetToken.symbol === 'btcb' ? BTCBStakeAmount : BTCSTStakeAmount
        );

        const totalAmount = BTCBStakeAmount.add(BTCSTStakeAmount);
        let hashrate = FixedNumber.from(
          utils.formatUnits(totalAmount, sourceToken.decimals) + ''
        );
        const sourceSupplyAmount: BigNumber = await sourceTokenERC20Contract.totalSupply();
        if (
          FixedNumber.from(totalAmount)
            .divUnsafe(FixedNumber.from(sourceSupplyAmount))
            .subUnsafe(FixedNumber.from(DISTRIBUTE_LINE))
            .isNegative()
        ) {
          hashrate = FixedNumber.from(DISTRIBUTE_LINE).mulUnsafe(
            FixedNumber.fromString(
              utils.formatUnits(sourceSupplyAmount, sourceToken.decimals) + ''
            )
          );
        }

        const mineBTCBStatus = await getMineStatus();
        const dailyBtcPerHash = FixedNumber.from(
          mineBTCBStatus.dayList[0].eachHaveCoin
        );
        const price = FixedNumber.from(
          '' + getPriceBySymbol(tokenPrice, BTCB.symbol)
        );
        console.log(
          'calculate netreward using pric:' +
            price +
            ' daily btc per TH:' +
            dailyBtcPerHash +
            ' hashrate:' +
            hashrate
        );

        const { btc, usd } = calculateDailyReward(
          hashrate,
          dailyBtcPerHash,
          price
        );
        const tps = btc.divUnsafe(hashrate);
        const ups = usd.divUnsafe(hashrate);
        setTokenRewardPerStake(tps);
        setUsdTokenRewardPerStake(ups);
      }
    } catch (e) {
      console.error(e);
      console.log('get apy error');
    } finally {
      setLoading(false);
    }
  }, [
    sourceToken,
    targetToken,
    sourceTokenERC20Contract,
    tokenPrice,
    library,
    account,
    txHappend
  ]);

  const apy = useMemo(() => {
    if (!loading && usdRewardPerStake) {
      return formatApy(
        usdRewardPerStake.divUnsafe(FixedNumber.fromString('10')),
        FixedNumber.fromString(
          '' + getPriceBySymbol(tokenPrice, sourceToken.symbol)
        )
      );
    }
  }, [usdRewardPerStake, tokenPrice, loading, txHappend]);

  return {
    loading,
    farmingInfo,
    sourceStakeAmount,
    usdRewardPerStake,
    tokenRewardPerStake,
    apy
  };
}
