import React, { FC, useEffect, useMemo } from 'react';
import {
  Typography,
  makeStyles,
  Grid,
  Box,
  TextField,
  Button,
  CircularProgress
} from '@material-ui/core';
import { useFormik } from 'formik';
import { useStakeState } from 'src/hooks/useStakeState';
import {
  commonFormatBalance,
  formatBalance,
  parseBalance
} from 'src/utils/format';
import { FixedNumber, utils, BigNumber } from 'ethers';
import { isEmptyValue } from 'src/utils';
import { useTranslation } from 'react-i18next';
import LoadingButton from 'src/components/LoadingButton';

interface UnstakeContentProps {
  state: any;
}

const useStyles = makeStyles(theme => ({
  root: {}
}));

const percentMap = [
  {
    title: '25%',
    value: 0.25,
    cal: [1, 4]
  },
  {
    title: '50%',
    value: 0.5,
    cal: [1, 2]
  },
  {
    title: '75%',
    value: 0.75,
    cal: [3, 4]
  },
  {
    title: '100%',
    value: 1,
    cal: [1, 1]
  }
];

const UnstakeContent: FC<UnstakeContentProps> = ({ state }) => {
  const classes = useStyles();
  const { t } = useTranslation();
  const {
    targetToken,
    yourStakeAmount,
    asyncUnstakeClick,
    sourceToken
  } = state;

  const formik = useFormik({
    initialValues: {
      amount: '0.0'
    },
    onSubmit: () => {}
  });

  const {
    handleChange,
    handleSubmit,
    isSubmitting,
    touched,
    values,
    errors,
    setFieldValue
  } = formik;

  const insufficientBalance = parseBalance(
    values.amount + '' || '0',
    sourceToken.decimals
  ).gt(yourStakeAmount.result || BigNumber.from(0));

  return yourStakeAmount.loading ? (
    <CircularProgress />
  ) : (
    <form className={classes.root} onSubmit={handleSubmit}>
      <Typography variant="h6">
        {t('mine.staking.youStaked')}:{' '}
        {yourStakeAmount.loading
          ? 'loading'
          : formatBalance(yourStakeAmount.result, sourceToken.decimals)}
      </Typography>
      <Box mt={2}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              type="number"
              name="amount"
              label={t('mine.staking.unstakedAmountInput')}
              error={Boolean(touched.amount && errors.amount)}
              helperText={touched.amount && errors.amount}
              fullWidth
              required
              value={values.amount}
              onChange={handleChange}
              variant="outlined"
            />
          </Grid>
          <Grid item container xs={12} sm={6}>
            {percentMap.map(item => (
              <Grid key={item.value} item xs={3}>
                <Button
                  variant="contained"
                  onClick={() => {
                    const m = item.cal[0];
                    const d = item.cal[1];
                    setFieldValue(
                      'amount',
                      utils.formatUnits(
                        yourStakeAmount.result
                          .mul(BigNumber.from(m))
                          .div(BigNumber.from(d)),
                        sourceToken.decimals
                      )
                    );
                  }}
                >
                  {item.title}
                </Button>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Box>
      <Box mt={2}>
        <LoadingButton
          fullWidth
          color="secondary"
          disabled={isEmptyValue(values.amount + '') || insufficientBalance}
          pending={asyncUnstakeClick.loading}
          size="large"
          type="submit"
          variant="contained"
          onClick={() =>
            asyncUnstakeClick.execute(
              parseBalance(values.amount + '', sourceToken.decimals)
            )
          }
        >
          {insufficientBalance
            ? t('mine.staking.insufficientBalance')
            : t('mine.staking.unstake')}
        </LoadingButton>
      </Box>
    </form>
  );
};

export default UnstakeContent;
